const swiper = new Swiper(".section-slider__wrapper", {
    loop: true,
    slidesPerView: 2,
    navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev",
        slidesPerView: 'auto',
      },
   
})

 let scroll = document.getElementsByClassName('materials__list-button');
 if (scroll == 'none') {
   document.getElementsByClassName('materials__list-descripton').style.display = "block";
 } else {
  document.getElementsByClassName('materials__list-descripton').style.display = "none";
 }
